import { ref as ref_database, query, limitToLast, set , onValue, off , child, get //limitToFirst 
 } from "firebase/database"
import { ref as ref_storage, uploadBytes , getDownloadURL } from "firebase/storage"
import { toRaw } from 'vue';
import { getDataBaseForLocal, getStorageForLocal }  from "./.env.js"

export default { 
    mounted() {
        this.observePage()
    }, 
    data() {
        return  {
            productsBy–°ategory: {
                "video_courses" : {},
                "music_instrument" : {},
                "concert_tickets" : {   },
                "clothes": {}
            }, 
            isModile: Boolean( ( window.innerWidth < 850)) ,
            database:  getDataBaseForLocal(),  //this.answerNetlify.database , 
            storage:  getStorageForLocal(),    // this.answerNetlify.storage , 
            productLimit: {
                "video_courses" : 1,
                "concert_tickets" : 2,
                "music_instrument" : 2,
                "clothes": 3,
                "mobile": 3
            },
            productIterator: {
                "video_courses" : 1,
                "concert_tickets" : 2,
                "music_instrument" : 2,
                "clothes": 3
            },
            listnerDataBase: {},
            isExitGet: false,
            isFind: true
        }
    },
    computed: {
        nameCategory() {
            return this.$route.params.id
          },
        idProduct() {
            return this.$route.params.idProduct
        },
        isError() {
          switch (true) {
            case this.isNotCategory:
              return true
            case this.isNotProduct:
              return true
            default:
              return false
          }
        },
        textError() {
          switch (true) {
            case this.isNotCategory:
                if ( this .nameCategory ) {
                    return `–í url —Å–∞–π—Ç–∞ –Ω–∞ –º–µ—Å—Ç–µ, –≤ –∫—Ç–æ—Ä–æ–º –¥–æ–ª–∂–Ω–∞ —É–∫–∞–∑—ã–≤–∞—Ç—å—Å—è –∫–∞—Ç–µ–≥–æ—Ä–∏—è –≤—ã —É–∫–∞–∑–∞–ª–∏: ${this.nameCategory }. –ö —Å–æ–∂–∞–ª–µ–Ω–∏—é, —Ç–∞–∫–æ–π –∫–∞—Ç–µ–≥–æ—Ä–∏–∏ —Ç–æ–≤–∞—Ä–æ–≤ –≤ –Ω–∞—à–µ–º –º–∞–≥–∞–∑–∏–Ω–µ –Ω–µ—Ç.`
                }
                return `–í url —Å–∞–π—Ç–∞ –Ω–∞ –º–µ—Å—Ç–µ, –≤ –∫—Ç–æ—Ä–æ–º –¥–æ–ª–∂–Ω–∞ —É–∫–∞–∑—ã–≤–∞—Ç—å—Å—è –∫–∞—Ç–µ–≥–æ—Ä–∏—è –≤—ã –Ω–∏—á–µ–≥–æ –Ω–µ —É–∫–∞–∑–∞–ª–∏.
                        –í—ã–±–∏—Ä–µ—Ç–µ –∫–∞—Ç–µ–≥–æ—Ä–∏—é –Ω–∞ –≤–µ—Ä—Ö–Ω–µ–π –ø–∞–Ω–µ–ª–∏ –∏–ª–∏ —É–∫–∞–∂–∏—Ç–µ —Å—É—â–µ—Å—Ç–≤—É—é—â—É—é –∫–∞—Ç–µ–≥–æ—Ä–∏—é.`
            case (this.isNotProduct && Boolean(this.idProduct) && this.isExitGet) :
                return `–í –∫–∞—Ç–µ–≥–æ—Ä–∏–∏ ${this.nameCategory}  –Ω–µ—Ç —Ç–æ–≤–∞—Ä–∞ —Å id = ${this.idProduct}`
            case (this.isNotProduct && Boolean(this.idProduct) && !this.isExitGet) :
                return `–ò—â—É —Ç–æ–≤–∞—Ä`
            case this.isFind:
                return `–ó–∞–≥—Ä—É–∑–∫–∞ —Ç–æ–≤–∞—Ä–æ–≤ ${this.nameCategory}`
            case this.isNotProduct:
                return `–í –∫–∞—Ç–µ–≥–æ—Ä–∏–∏ ${this.nameCategory}  –ø–æ–∫–∞ –Ω–µ—Ç —Ç–æ–≤–∞—Ä–æ–≤. –ë—É–¥—å—Ç–µ –ø–µ—Ä–≤—ã–º!`
            default:
                return ""
          }
        },
        isNotCategory() {
  
          if ( !this.productsBy–°ategory[this.nameCategory] ) {
            return true
          } else {
            return false
          }
  
        },
        isNotProduct() {
          if ( Object.keys( this.productsBy–°ategory[this.nameCategory]).length === 0 ) {
            return true
          }
          return false
        },
       
    },
    methods: {
        writeProduct( product, category ) {

            set(    ref_database(  
                            toRaw( this.database) ,
                            `productList/${category}/${Date.now()}`
                        ),  
                    product 
                );
    
        },
        listenNewProduct() {
            
            this.isFind = true
            
            Object.keys( this.productLimit ).forEach(
                ( el ) => {
                   
                    if ( el === this.nameCategory ) {
                        
                   
                        let productLimit 
                        this.isModile  ? productLimit = this.productLimit["mobile"] : productLimit = this.productLimit[el]  

                        const productListRef = query( ref_database( toRaw(this.database), `productList/${el}` ), 
                                                limitToLast(productLimit),
                                            );

                       
                        onValue( productListRef , ( snapshot ) => {
                        
                        
                            snapshot.forEach(element => {
                            
                            const keyName = element.key
                            const data = element.val()
                            
                            this.productsBy–°ategory[el][keyName] = data
                             
                            });

                            this.isModile  ? this.productLimit["mobile"] += 1 : this.productLimit[el] += this.productIterator[el]
                            this.isFind = false
            })
                    } 
                    if ( el !== this.nameCategory ) {

                        const productListRef = query( ref_database( toRaw(this.database), `productList/${el}`) );
                        off(productListRef);

                    }
                }
            )

        
        },
        getNewProduct(productId) {
            
            const productRef = ref_database( toRaw(this.database))

            get( child( productRef , `productList/${this.nameCategory}/${productId}`) )
                .then((snapshot) => {

                    if (snapshot.exists()) {

                        const data = snapshot.val();    
                        this.productsBy–°ategory[this.nameCategory][productId] = data

                    } 

                    this.isExitGet = true

                })
                .catch((error) => {
                    console.error(error);
                })
                .finally(()=>{
                    this.isExitGet = true
                })

        },
        async uploadFile(file, property, category) {

            const firebaseUrl =  `${property}/${category}/${Date.now()}`
    
            await uploadBytes( ref_storage( toRaw( this.storage ), firebaseUrl ), file)
                .then(() => {
                    console.log('Uploaded file!');
            });
    
            const imageURL =  await getDownloadURL(ref_storage( toRaw(this.storage), firebaseUrl))
                .then((url) => {
                    return url
                })
                .catch((error) => {
                    console.log(error);
                });
            
            return imageURL
        },
        
        async callNetlify() {

            const url = "/.netlify/functions/getDataBase/"

            try {
                const response= await fetch(url) //–µ—Å–ª–∏ –≤—ã–∑–æ–≤ —Ñ—É–Ω–∫—Ü–∏–∏ –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω—ã–π –∏–¥—ë—Ç –æ–∂–∏–¥–∞–Ω–∏–µ

                if (!response.ok) {
                    throw new Error(`–æ—à–∏–±–∫–∞ –≤ url`)
                }
                
                return await response.json()
    
            } catch(errors) {
                console.log(errors)
            }
            finally{
                console.log("finaly");
            }

        },
        observePage() {

            const options = {
                rootMargin: '0px',
                threshold: 1.0
            }
            
            const callback = ( entaries ) => {

                if (entaries[0].isIntersecting ) {
                    this.listenNewProduct()
                }
            } 
              
            const observer = new IntersectionObserver(callback, options);

            if ( this.$refs?.observer )  { observer.observe(this.$refs.observer) }    
            
        }
    },

}










