<template>

 
<div class = "main-pannel">
  
  <h3 class = "topic-mp">  –ü–∞–Ω–µ–ª—å —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è </h3>

    <div>
      <BlueButton 
        @click="clicktoBack()" 
        textButton = "–í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞–∑–∞–¥"> 
      </BlueButton>

      <BlueButton
        v-if ="isShow.saveButton"
          @click="saveResults()" 
          textButton = "Co—Ö—Ä–∞–Ω–∏—Ç—å"> 
      </BlueButton>

    </div>
  
</div>


 
<div class="check-box"
:style = "{ 'border': style[counter]?.border }">

  <div class = "check-button">

  </div>
  
  <div class="info-quest">

   <div>
    {{ textInfo }}
   </div>

  </div>
  <div
      v-for="questEl, qestI in questionary"
          :key ="qestI">

    <div v-if ="qestI===counter">

      <h4 class="topic-quest"
        :style = "style[qestI]">
         {{ questEl.question}}
      </h4>

      <select 
          class = "select-quest"
          v-model="answers[qestI]">

            <option disabled value="">
              –í—ã–±–∏—Ä–∏—Ç–µ –∏–∑ –≤–∞—Ä–∏–∞–Ω—Ç–æ–≤
            </option>

            <option 
              v-for="el, i in questEl.variantAnswers"
              :key = "i">
                <div v-if ="el"
                 @click="nextQuest"> {{ el.name }} </div>
            </option>
                  
      </select>
    </div>
    
   
  </div>

</div>
  

</template>

<script>

export default {
    name: "Questionary",
    data() {
        return {
  
        counter: 0,
        answers: [],
        newNameCategoryArray: [], 
        nameCategoryArray: this.$store.state.nameCategoryArray,
        nameCategoryObj: { ...this.$store.state.nameCategoryArray },
        style:
          [
            {
              'background-color': 'rgba(255, 217, 0, 0.5)',
              'border': 'solid 3px rgb(215, 183, 5)',
            },
            {
              'background-color': 'rgba(243, 236, 236, 0.5)',
              'border': 'solid 3px rgba(111, 107, 127, 0.6)',
            },
            {
              'background-color': 'rgba(189, 134, 33, 0.7)',
              'border': 'solid 3px black',
            },
            
          ]
        }
    },
    computed:{
      nameCategory() {
            return this.$route.params.id
      },
      nowAnswer(){
        return this.answers[this.counter]
      },
      questionary() {
        return [
            { 
              question: '–ö–∞–∫–∞—è –∏–∑ —ç—Ç–∏—Ö –∫–∞—Ç–µ–≥–æ—Ä–∏–π –∫–∞–∂–µ—Ç—Å—è –≤–∞–º –Ω–∞–∏–±–æ–ª–µ–µ –∏–Ω—Ç–µ—Ä–µ—Å—Ç–Ωo–π?',
              variantAnswers: this.nameCategoryObj,
            },
            { 
              question: '–¢–µ–ø–µ—Ä—å –≤—ã –º–æ–∂–∏—Ç–µ –≤—ã–±—Ä–∞—Ç—å –∫–∞—Ç–µ–≥–æ—Ä–∏—é, –∫–æ—Ç–æ—Ä–∞—è –±—ã–ª–∞ –±—ã –∏–Ω—Ç–µ—Ä–µ—Å–Ω–µ–µ –æ—Å—Ç–∞–≤—à–∏—Ö—Å—è',
              variantAnswers: this.nameCategoryObj,
            },
            { 
              question: '–°–µ–π—á–∞—Å –≤—ã –≤—ã–±–∏—Ä–∞–µ—Ç–µ —Ç—É –∫–∞—Ç–µ–≥–æ—Ä–∏—é, –∫–æ—Ç–æ—Ä–∞—è –∑–∞–π–º—ë—Ç –±—Ä–æ–Ω–∑–æ–≤–æ–µ –º–µ—Å—Ç–æ',
              variantAnswers: this.nameCategoryObj,
              answers: ""
            },
            { 
              question: '–í—ã–±–µ—Ä–µ—Ç–µ 4 –º–µ—Å—Ç–æ :)',
              variantAnswers: this.nameCategoryObj,
            }

          ]
          
      },
      textInfo(){
        return `–°–µ–π—á–∞—Å –≤—ã –º–æ–∂–∏—Ç–µ –ø–æ—Å–ª–µ–¥–æ–≤–∞—Ç–µ–ª—å–Ω–æ –≤—ã–±—Ä–∞—Ç—å 4 –∫–∞—Ç–µ–≥–æ—Ä–∏–∏, –ø–æ—Å–ª–µ —á–µ–≥–æ –ø–æ—è–≤–∏—Ç—Å—è –∫–Ω–æ–ø–∫–∞ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è 
                , –µ—Å–ª–∏ –≤—ã –Ω–∞–∂–º—ë—Ç–µ –Ω–∞ –Ω–µ—ë –æ–Ω–∏ –±—É–¥—É—Ç —Ä–∞—Å–ø–æ–ª–∞–≥–∞—Ç—å—Å—è –Ω–∞ –≤–µ—Ä—Ö–Ω–µ–π –ø–∞–Ω–µ–ª–∏ –≤ —ç—Ç–æ–º –∂–µ –ø–æ—Ä—è–¥–∫–µ.`
      },
      isShow() {
          if( this.counter === this.nameCategoryArray.length ) {
            return { saveButton: true }
          }
          return { saveBautton: false }
      },
    },
    methods: {
      clicktoBack() {
        this.$router.go(-1)
      },
      nextQuest(value) {
        if ( this.answers[this.counter] ) {

            const indexForDelete = this.nameCategoryArray.findIndex(el => el.name === value )

            this.newNameCategoryArray.push(this.nameCategoryObj[indexForDelete])
            delete this.nameCategoryObj[indexForDelete]

            this.counter++
        }
      },
      saveResults(){
       
        this.$store.dispatch('saveObjToLocalStor', { 'nameCategoryArray': this.newNameCategoryArray } );  
        this.isShow.saveButton = false  
      }
    },
    watch: {
      nowAnswer(value){
       
        this.nextQuest(value)
        this.$store.commit('update_newNameCategoryArray', this.newNameCategoryArray )
      }
    }
  
}
</script>

<style>

.check-button{
  width: 60%;
  margin: auto;
  display: flex;
}

.check-button button{
  margin: auto;
}

.check-box{
  box-sizing: border-box;
  padding: 50px;
  width: 50%;
  margin: 20px auto;

  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  border: solid 4px   rgb(200, 172, 16);
  box-shadow: 0px 0px 10px 1px white; 

  text-align: center;
}
.info-quest{
  box-sizing: border-box;
  padding: 20px;
  width: 70%;
  margin: 30px auto;
  font-size: 120%;
  
  background-color: rgba(214, 212, 107, 0.4);
  box-shadow: 0px 1px 4px 1px black; 
  border: solid 2px   gold;
  border-radius: 1px;
}


.select-quest{
  font-size: 110%;
  text-align: center;
  border-radius: 5px;
  border: solid 2px black;
}
.topic-quest{
  width: 60%;
  margin: 2% auto;
  border-radius: 10px;
  box-shadow: 0px 1px 5px 2px rgb(119, 119, 119); 

  padding: 1%;
}
.topic-quest {
  background-color: rgba(21, 159, 233, 0.7);
  border: solid 2px rgb(4, 153, 116, 0.9);
}


.answer {
  position: static;
  box-sizing: border-box;
  padding-top: 1%;
}

@media (max-width: 700px){

  .check-box{
    width: 90%;
    padding: 15px 0;
  }

  .info-quest{
  padding: 20px;
  width: 90%;
  font-size: 90%;
}

}

</style>
